@import "variables";
@import "layout/normalize";

/*Common*/

* {
    margin: 0;
    padding: 0;

    outline: 0;
    box-sizing: inherit;

    &:before,
    &:after {
      box-sizing: inherit;
    }
}

html {
    -webkit-tap-highlight-color: transparent;
    font-family: sans-serif;
    font-size: 16px;
}

body, html {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-width: 320px;
}

img { max-width: 100% }

body {
    -ms-overflow-style: scrollbar;
}

button { 
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

textarea {
	resize: none;
}

 a, a:hover, a:active, a:focus,
button, input, button:hover, button:active, button:focus,
 textarea {
	text-decoration: none;
	outline: none;
	color: #323232;
}

body {
	font-size: 1rem;
	font-family: 'Montserrat';
	color: #fff;
	position: relative;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.container {
	padding: 0;
	margin: 0 auto;
	max-width: 90vw;
}

.container-common {
	max-width: 1170px;
	padding: 0 15px;
	margin: 0 auto;
}

img {
	height: auto;
}

