/*for anim section*/

.section-bg {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	transition: 1s;
}

/*try*/

.screen-section {
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: none;
	padding-top: 105px;
	overflow: hidden;
}

.section-active {
	display: block;
}

#screen {
	width: 100vw;
	height: 100vh;
}

.anim-section {
	z-index: 10;
	position: relative;
	opacity: 0;
}

.section-bg-move {	
	animation: bg-move 1s forwards;
}

.section-inner-move {
	animation: inner-in 1s forwards;
}

@keyframes bg-move {   
  0% {     
    transform: scale(1);   
  }   
   
  100% {
    transform: scale(1.1);
   }
}

@keyframes inner-in {   
  0% {     
    opacity: 0;   
  }   
   
  100% {
    opacity: 1;
   }
}

