.aside-nav {
	height: 100%;
	top: 0;
	position: fixed;
	right: 0;
	bottom: 0;
	// width: 119px;
	width: get-vw(119px);
	z-index: 20;
}

.aside-nav::before {
	content: "";
	display: block;
	position: absolute;
	height: 30%;
	width: 1px;
	background-color: rgba(119, 121, 126, 0.3);
	top: 0;
	left: 15px;
	z-index: 1;
}

.aside-scroll {
	position: absolute;
	width: 31px;
	height: 43px;
	background: url(../images/mouse.png) no-repeat center center / contain;
	bottom: 12%;
}

.aside-scroll_white {
	background: url(../images/mouse2.png) no-repeat center center / contain;
}

.aside-scroll::before {
	content: "";
	display: block;
	position: absolute;
	height: 10vh;
	width: 1px;
	background-color: rgba(119, 121, 126, 0.3);
	bottom: 0;
	left: 15px;
	z-index: 1;
	transform: translateY(-65%);

	background: linear-gradient(to bottom, rgba(119, 121, 126,.0), rgba(119, 121, 126,.8));
	background-position: 0 0;
	animation: background 1s infinite;
}

.aside-scroll::after {
	content: "";
	display: block;
	position: absolute;
	height: 10vh;
	width: 1px;
	background-color: rgba(119, 121, 126, 0.3);
	bottom: 0;
	left: 15px;
	z-index: 1;
	transform: translateY(115%);

	background: linear-gradient(to top, rgba(119, 121, 126,.0), rgba(119, 121, 126,.8)) ;
	background-position: 0 0;
	animation: background2 1s infinite;
}

@keyframes background {
  100% {
    background-position: 0 100px;
  }
}

@keyframes background2 {
  100% {
    background-position: 0 -100px;
  }
}

.aside-nav ul {
	list-style: none;
	position: absolute;
	// right: 85px;
	right: get-vw(119px);
	top: 37%;
	padding-right: 80px;
	z-index: 90;
	transform: translateX(34px);
}

.aside-nav ul a {
	font-size: 0.875rem;
	color: #6c7280;
	font-family: 'Montserrat M';
	position: relative;
	white-space: nowrap;
	transition: font-size 0.3s ease-in;
}

.aside-nav ul li {
	margin-bottom: 32px;
	text-align: right;
	position: relative;
}

.aside-nav ul li.current a {
	font-size: 1.125rem;
	color: #fff;
}

.aside-nav ul a::after {
	content: "";
	display: block;
	position: absolute;
	 border-width: 1px;
  border-color: #6c7280;
  border-style: solid;
  width: 20px;
  height: 0;
  right: -32px;
  top: 50%;
}

.aside-nav ul li.current a::after {
	border-color: #fff;
}

.aside-nav ul li .aside-dot {
	display: block;
	position: absolute;
	border-radius: 50%;
  background-color: #fff;
  opacity: 0.4;
  width: 7px;
  height: 7px;
  right: -65px;
  top: 50%;
  transform: translateY(-50%);
}


.aside-nav ul li.current .aside-dot {
  background-color: rgb(12, 99, 61);
  width: 10px;
  height: 10px;
  opacity: 1;
 }

.aside-nav ul li.current .aside-dot::after {
	border-width: 1px;
  border-color: #6c7280;
  border-style: solid;
  border-radius: 50%;
  position: absolute;
  content: "";
  display: block;
   width: 20px;
  height: 20px;
  transform: translate(-50%, -50%) scale(0.2);
  top: 50%;
  left: 5px;
	animation: buble-span 0.3s ease-in forwards;
}

@keyframes buble-span {   
  0% {     
       
  }   
   
  100% {
  	transform: translate(-50%, -50%) scale(1);
   }
}


