  color: #fff;
  font-family: 'Montserrat M';
}

.checkbox_text:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 17px;
  height: 17px;
  background-color: #2aace2;
  transform: translateY(-50%);
}

.checkbox_text:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0px;
  width: 17px;
  height: 17px;
  transform: translateY(-50%);
}

.checkbox input:checked + .checkbox_text:after {
  background: url(../images/checked.png) no-repeat center center;
}

